<template>
  <v-app>
    <Header @delete-local-storage="deleteLocalStorage" />
    <v-main>
      <v-container>
        <router-view
          @add-book-list="addBook"
          @update-book-info="updateBookInfo"
          :books="books"
        />
      </v-container>
    </v-main>
    <Footer />
  </v-app>
</template>

<script>
import Header from "./global/Header";
import Footer from "./global/Footer";

// ローカルストレージの操作をここに記述
const STRAGE_KEY = "books";

export default {
  name: "App",

  components: {
    Header,
    Footer,
  },

  data: () => {
    return {
      books: [],
      newBook: null,
    };
  },
  mounted() {
    if (localStorage.getItem(STRAGE_KEY)) {
      try {
        this.books = JSON.parse(localStorage.getItem(STRAGE_KEY));
      } catch (e) {
        localStorage.removeItem(STRAGE_KEY);
      }
    }
  },
  methods: {
    addBook(e) {
      if (!e) {
        return;
      }

      this.books.push({
        id: this.books.length,
        title: e.title,
        image: e.image,
        description: e.description,
        readDate: "",
        memo: "",
      });
      // this.newBook = "";
      this.saveBooks();
      // 最後に追加した要素のidを取得し、遷移する
      // console.log(this.books.slice(-1)[0].id)
      this.goToEditPage(this.books.slice(-1)[0].id);
    },
    removeBook(x) {
      this.books.splice(x, 1);
      this.saveBooks();
    },
    saveBooks() {
      const parsed = JSON.stringify(this.books);
      localStorage.setItem(STRAGE_KEY, parsed);
    },
    updateBookInfo(e) {
      // 打ちあがってきた、オブジェクトを新しいオブジェクトに入れる。
      const updateInfo = {
        id: e.id,
        readDate: e.readDate,
        memo: e.memo,
        // もとあらあった値もここで入れてしまう！！！！！
        title: this.books[e.id].title,
        image: this.books[e.id].image,
        description: this.books[e.id].description,
      };
      // thisの中に適応！！！！！
      // 新しい配列で置き換える必要があるので、spliceで置き換える！！！！！！
      this.books.splice(e.id, 1, updateInfo);
      this.saveBooks();
      this.$router.push("/");
    },
    goToEditPage(id) {
      this.$router.push(`/edit/${id}`);
    },
    deleteLocalStorage() {
      const isDeleted = "LocalStrageのデータを削除してもよろしいですか？";
      if (confirm(isDeleted)) {
        localStorage.setItem(STRAGE_KEY, "");
        localStorage.removeItem(STRAGE_KEY);
        this.books = [];
        window.location.reload();
      }
    },
  },
};
</script>
