<template>
  <div>
    <!-- $route.paramsでは、URLから渡された情報が返る！！！！！！！！！！！！！ -->
    <v-row>
      <v-col cols="12">
        <v-card class="mx-auto">
          <v-row>
            <v-col cols="12" sm="4">
              <v-img :src="book.image"></v-img>
            </v-col>
            <v-col cols="12" sm="8">
              <v-row>
                <!-- タイトル -->
                <v-col cols="12">
                  <v-card-title> タイトル： {{ book.title }} </v-card-title>
                </v-col>
                <!-- 読んだ日 -->
                <v-col cols="6" sm="4">
                  <div class="sub_title">読んだ日：</div>
                  <v-menu
                    v-model="menu2"
                    :colse-on-content-click="false"
                    :nudge-right="40"
                    transition="scale-transition"
                    offset-y
                    min-width="auto"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="date"
                        prepend-icon="mdi-calendar"
                        readonly
                        v-bind="attrs"
                        v-on="on"
                        class="pl-2"
                      ></v-text-field>
                    </template>
                    <v-date-picker
                      v-model="date"
                      @input="menu2 = false"
                    ></v-date-picker>
                  </v-menu>
                </v-col>
                <!-- 感想 -->
                <v-col cols="12">
                  <div class="sub_title">感想：</div>
                  <v-textarea class="mx-2" v-model="book.memo">
                    {{ book.memo }}
                  </v-textarea>
                </v-col>
                <!-- ボタン -->
                <v-col cols="12">
                  <v-card-actions>
                    <v-btn color="secondary" to="/"> 一覧に戻る </v-btn>
                    <v-btn color="info" @click="updateBoolInfo">
                      保存する
                    </v-btn>
                  </v-card-actions>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
export default {
  name: "BookEdit",
  props: {
    books: Array,
  },
  data() {
    return {
      book: "",
      date: "",
      menu2: false,
    };
  },
  methods: {
    updateBoolInfo() {
      this.$emit("update-book-info", {
        id: this.$route.params.id,
        readDate: this.date,
        memo: this.book.memo,
      });
    },
  },
  // マウントされる前に、必要なデータを読み込むためのもの！！！！！！！！ログインしていないユーザーに制限を加える機能も実装できる！！！！！！！
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      // DOMが更新されるのを待ってから、その操作を行う！！！！！
      // nextTickは、プロミスを返すので、thenを続けてその後の操作を行う！！！！！
      vm.$nextTick().then(() => {
        vm.book = vm.books[vm.$route.params.id];
        if (vm.book.readDate) {
          vm.date = vm.book.readDate;
        } else {
          vm.date = new Date(
            Date.now() - new Date().getTimezoneOffset() * 60000
          )
            .toISOString()
            .substr(0, 10);
        }
      });
    });
  },
};
</script>

<style>
.sub_title {
  margin-left: 8px;
}
</style>
